    /*src: url("../fonts/Raleway-Regular.ttf");*/
    /*src: url("../fonts/Raleway-Thin.ttf");*/
    /*src: url("../fonts/Raleway-Bold.ttf");*/
}

@font-face {
    font-family: Raleway;
    font-weight: 300;
    src: url(/Roomlink-Frontend/static/media/Raleway-Thin.9f5eec4e.ttf);
